.profile{
    background-color: white;
    border-radius: 6px;
    width: 100%;
    display: inline-block;
    /*-moz-border-radius: 6px;
    -webkit-border-radius: 6px;
    -o-border-radius: 6px;*/
}
.profile_name{
    line-height: 18px;
    padding: 20px;
}
.profile_name .name{
    text-transform: uppercase;
    font-size: 15px;
    font-weight: bold;
}
.profile_name .title{
    font-weight: 12px;
    color: #777;
}
.profile_img{
    margin-top: -15px;
    -webkit-clip-path: polygon(0 9%, 100% 0, 100% 94%, 0% 100%);
    clip-path: polygon(0 9%, 100% 0, 100% 94%, 0% 100%);
}

.profile_img img {
    width: 100%;
  }

.profile_info{
    top: 0;
    left: 0;
    margin: -45px 8px 25px;
}
.profile_info .timline .timline_content{
    margin-top: 2px;
    margin-bottom: 10px;
}
.timelineItem_text{
    font-size: 13.5px !important;
    color: #787878 !important;
    font-weight: 200 !important; 
}

.timelineItem_text > a {
    text-decoration: none;
    color: #787878 !important;
}

.timlineItem_text >span{
    color: black;
}

.profile .button_container {
    width: 100%;
    display: flex;
    justify-content: center;
    margin-bottom: 30px;
  }